.about-description {
  width: 48%;
  @media (max-width: 700px) {
    width: 100%;
  }
  p {
    font-size: 20px;
    line-height: 1.3;
    @media (max-width: 500px) {
      font-size: 18px;
    }
  }
}
.about {
  display: flex;
  justify-content: space-between;
  border-radius: 50px;
  background: #3d4056;
  padding: 30px;
  height: 50vh;
  min-height: 500px;
  display: flex;
  align-items: center;
  margin-bottom: 7px;
  @media (max-width: 700px) {
    flex-direction: column;
    height: auto;
  }
  .stack {
    font-size: 21px;
    margin-bottom: 30px;
    @media (max-width: 700px) {
      margin-bottom: 10px;
    }
    @media (max-width: 400px) {
      font-size: 18px;
    }
  }
  .tech {
    display: flex;
    flex-wrap: wrap;
    width: 48%;
    flex-direction: column;
    @media (max-width: 700px) {
      width: 100%;
    }
  }
  .tech-stack {
    display: flex;
    flex-wrap: wrap;
    padding-left: 20px;
    margin-bottom: 15px;
    justify-content: space-between;
  }
  .tech-stack li {
    font-size: 19px;
    list-style: square;
    width: 200px;
    @media (max-width: 700px) {
      width: 160px;
    }
    @media (max-width: 400px) {
      width: 130px;
      font-size: 17px;
    }
  }
}
